								</view>

							</view>
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 100%;width: 100%;">
						<view class="page-box">
							<view class="u-config-wrap" style="padding: 0 40rpx;">
								<!-- <view class="u-config-item">
									<view class="u-item-title">图片示例</view>
								</view> -->

